
.cart{
    margin-top: 70px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.cartItem{
    width: 100%;
    height: 250px;
    display: flex;
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px
}
.cartItem img{
    width: 200px;
}
.cartItem .description{
    width: 100%;
    font-size: 30px;
}
.countHandler input {
    width: 40px;
    text-align: center;
    font-weight: bolder;
}
.checkout button {
    width: 150px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 8px;
    margin: 10px;
    cursor: pointer;
}
@media screen and (min-width: 767px){
    .cartItem{
        /* width: 86%; */
        height: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
        border-radius: 25px;
        margin: 30px
    }
    .cartItem .description{
        font-size: 20px;
        /* width: 86%; */
    }
    .cartItem .img{
        width: 150px;
    }
}